.ng2-c-card {
  background: #ffffff;
  border-radius: 4px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  color: #28648a; }
  @media (min-width: 768px) {
    .ng2-c-card {
      margin: 10px 0;
      width: 300px; } }

.ng2-c-card--no-data {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 268px;
  justify-content: center; }

.ng2-c-card--no-shadow {
  box-shadow: none; }

.ng2-c-card__header {
  align-items: center;
  background: #28648a;
  border-radius: 4px 4px 0 0;
  display: flex;
  justify-content: space-between;
  padding: 5px 10px; }

.ng2-c-card__heading {
  color: #ffffff; }

.ng2-c-card__content {
  padding: 0 10px; }

.ng2-c-card__definition {
  border-top: 1px solid #f5f5f5;
  padding: 10px 0;
  text-transform: uppercase; }

.ng2-c-card__definition--value .ng2-c-card__value {
  font-size: 32px; }

.ng2-c-card__definition--return .ng2-c-card__value {
  font-size: 32px;
  line-height: 1; }

.ng2-c-card__key {
  font-size: 12px;
  padding-bottom: 10px; }

.ng2-c-card__value {
  align-items: center;
  color: #4a4a4a;
  display: flex;
  font-size: 14px;
  justify-content: space-between; }

.ng2-c-card__value__icon {
  color: #28648a;
  line-height: inherit; }

.ng2-c-card__split {
  display: flex;
  justify-content: space-between; }
  .ng2-c-card__split .ng2-c-card__definition {
    flex-grow: 1; }
    .ng2-c-card__split .ng2-c-card__definition:last-child {
      border-left: 1px solid #f5f5f5;
      padding-left: 10px; }
